<script>
	export let itens;

	const excluir = (i) => {
		itens.splice(i, 1)
		itens = itens;
	}

	const feito = () => {
		console.log("msg")
	}

	const teste = (checkbox) => {
		let th = checkbox.parentElement;
		let tr = th.parentElement;
		tr.querySelector("td").classList.toggle("feito svelte-1444hhz");

	}

</script>

<style>
	/*Riscar itens da lista*/
	.feito { text-decoration: line-through; }
</style>

{#each itens as {nome}, i}
<tr>
	<th><input type="checkbox" on:click={() => teste(this)}></th>

	<td class="feito">{nome}</td>
	<th><button on:click={() => excluir(i)} class="button is-danger">Excluir</button></th>
</tr>
{/each}